:host {
  display: block;
}

.medtango-example {
  &__button {
    padding: 8px 12px;
    background: lightcoral;
    color: #222;
    font-weight: 600;
    font-size: 20px;
    border: 2px solid #aaa;
    border-radius: 8px;
    cursor: pointer;
    transition: transform 300ms ease-in-out;

    &:active {
      transform: scale(0.95);
    }
  }
}
